body{
  font-family: Arial, Helvetica, sans-serif;
}
.timetable-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    position: relative;
  }
  
  .popup-form {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .popup-form-content {
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 80%;
    max-width: 600px;
    position: relative;
  }
  
  .popup-form-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #888;
    transition: color 0.3s ease;
  }
  
  .popup-form-close:hover {
    color: #333;
  }
  
  .form-row {
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
  }
  
  .form-row label {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .form-row input {
    margin-top: 5px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .form-actions {
    display: flex;
    gap: 10px;
  }
  
  .delete-btn {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .cancel-btn {
    background-color: #f0f0f0;
    color: #333;
    border: 1px solid #ddd;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .controls {
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed; 
  }
  
  table th, table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
    width: calc(100% / (1 + 6)); 
  }
  
  .empty-slot {
    background-color: lightblue;
    cursor: pointer;
  }
  
  .highlighted-cell {
    background-color: darkblue;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
  }
  
  table td {
    cursor: pointer;
  }

  /* Make inputs bigger and move the labels above the inputs */
.controls label {
    display: block; /* Label above the input */
    font-size: 16px; /* Increase font size for better visibility */
    margin-bottom: 5px;
  }
  
  .controls input, .controls select {
    width: 100%; /* Make inputs stretch across the container */
    padding: 12px; /* Increase padding for larger inputs */
    font-size: 16px; /* Larger font for better readability */
    border-radius: 5px;
    border: 1px solid #ccc;
    box-sizing: border-box; /* Ensures padding is included in width */
    margin-bottom: 15px; /* Add space between inputs */
  }
  
  .controls select {
    height: 45px; /* Make dropdowns consistent with input size */
  }
  
  /* Table styling */
  table {
    width: 100%;
    border-collapse: collapse;
    table-layout: auto; /* Allows for dynamic resizing */
    margin-top: 20px; /* Add some space above the table */
  }
  
  table th, table td {
    padding: 15px;
    text-align: center;
    border: 1px solid #ddd;
    font-size: 16px; /* Increase font size for better readability */
  }
  
  table th {
    background-color: #f4f4f4;
    font-weight: bold;
    color: #333;
  }
  
  table td {
    background-color: #fff;
    transition: background-color 0.3s ease; /* Add smooth transition for hover effect */
  }
  
  /* Styling for empty slots and booked slots */
  .empty-slot {
    background-color: #e6f2ff;
    cursor: pointer;
    font-weight: normal;
  }
  
 
  
  /* Hover effects for table cells */
  table td:hover {
    background-color: #ddd; /* Light grey on hover */
  }
  
  
  
 
  
